export default {
  name: 'login',
  data () {
    return {
      agree: true
    }
  },
  created(){

  },
  methods: {
    go_protocol(){
      window.location.href = this.url.host + this.url.api.protocol + this.cookie.get("token")
    },
    login(){
      window.location.href = this.url.api.getCode;
    }
  }
}



